* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

header {
  background-color: var(--main-color);
  width: 100%;
  z-index: 10;
  padding: .5rem 1rem;
  position: fixed;
  box-shadow: 0 3px 6px 1px #00000080;
}

.nav-main {
  max-width: 992px;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.nav-logo {
  height: 65px;
  width: auto;
}

.nav-logo img {
  height: 100%;
}

.nav-links {
  align-items: center;
  display: flex;
}

.nav-links ul {
  flex-direction: row;
  align-content: space-around;
  display: flex;
}

.nav-links ul li {
  padding-left: 3rem;
}

.bg-video {
  opacity: .7;
  z-index: -1;
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.main-contain {
  min-height: 100vh;
  height: auto;
  max-width: 992px;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  padding: 95px 1rem 2rem;
  display: flex;
}

.work-area > img {
  object-fit: cover;
  opacity: .8;
  width: 100%;
  height: 100vh;
  margin-top: -95px;
}

.categories {
  grid-column-gap: 1rem;
  grid-row-gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.container-cat {
  background-color: var(--main-color-light);
  height: auto;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 1.5px 6px 1px #00000040;
}

.container-cat:hover {
  background-color: var(--main-color);
  transition: all .2s;
}

.container-cat:hover h2 {
  margin: 20px 0;
  transition: all .2s;
}

.container-cat:hover .items-img {
  height: 80px;
  transition: all .2s;
}

.items-img {
  width: 100%;
  height: 120px;
  border-radius: 8px 8px 0 0;
  margin-bottom: .5rem;
  overflow: hidden;
}

.items-img img, .cab-detalle img, .detalle-img img {
  object-fit: cover;
  opacity: .8;
  width: 100%;
  min-height: 100%;
}

.cab-detalle {
  height: 190px;
  width: 100%;
  background-color: #000;
  position: relative;
  overflow: hidden;
}

.cab-detalle .title {
  width: 300px;
  text-align: center;
  position: absolute;
  top: 140px;
  left: calc(50vw - 150px);
}

.noPadding {
  padding-top: 0;
}

.detalle-img {
  width: 100%;
  padding: 1rem 0;
  overflow: hidden;
}

.detalle-img img {
  max-height: 450px;
  opacity: inherit;
  border: 1px solid #000;
  border-radius: 10px;
}

@media (max-width: 600px) {
  .nav-main {
    flex-wrap: wrap;
    justify-content: center;
  }

  .nav-links ul li {
    padding: 0 1rem;
  }

  .main-contain {
    display: inherit;
    padding-top: 130px;
  }

  .categories {
    grid-column-gap: 1rem;
    grid-row-gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
    display: grid;
  }

  .noPadding {
    padding-top: 0;
  }

  .work-area > img {
    margin-top: -120px;
    right: 250px;
  }
}

